 * @Author: zhaoye 
 * @Date: 2017-01-19 17:08:11 
 * @Last Modified by: renqingyue
 * @Last Modified time: 2017-04-20 13:35:51
 */
<template>
    <page>
        <cnav :title="'toast组件'" :next="'button'"></cnav>
        <div class="content">
            <cbutton @click.native="onClick1" class="default reverse">点击弹出模态框2222</cbutton>
            </br>
            <cbutton @click.native="onClick2" class="default reverse">模态框填充自定义html</cbutton>
            </br>
            <cbutton @click.native="onClick3" class="default reverse">自定义modal作为局部组件</cbutton>
            </br>
            <cbutton @click.native="onClick4" class="default reverse">自定义modal全局组件</cbutton>
            <modal-spa></modal-spa>
            
        </div>

        <!--这里是自定义局部组件-->
        <cmodal :title="'title'" :ok="'ok'" :cancel="'cancel'" :show="cmodalShow" @ok="onModalClick" @cancel="onModalClick">
            <div slot="content" >
                <p>custom-slot-content</p>
                <p>do whatever you want</p>                
            </div>
        </cmodal>
        
    </page>
</template>
<script>
import Vue from 'vue';
import {Page, Button, Modal,CModal} from 'gome-ui-kit';
import CModal2 from './modal.custom.vue';
import Nav from '../widgets/nav.vue';
// import  '../../index.js';

// import ModalSPA from '../../components/modal/modal.spa.vue';
export default Vue.extend({
    components: {
        cmodal: CModal,
        page: Page,
        cbutton: Button,
        cnav: Nav,
    },
    data () {
        return {
            cmodalShow: false
        }
    },
    methods: {
        onClick1 () {
            var data = {
                title: '标题',
                content: '内容',
                hasClose: true,
                ok: '确定aa',
                cancel: '取消bb',
            }
             this.$Modal(data,()=>{
                 this.$root.$refs['gome-ui-kit-modal'].show = false;
             })
        },
        onClick2 () {
            new Modal({
                data: {
                    htmlContent: `
                                    <ul>
                                        <li><span><</span>ul<span>></span></li>
                                        <li><span><</span>li<span>></span></li>
                                        <li><span><</span>li<span>></span></li>
                                        <li><span><</span>li<span>></span></li>
                                        <li><span><</span>li<span>></span></li>
                                    </ul>
                    `,
                    ok: '确定',
                    cancel: '取消',
                }
            })
        },
        onClick3 () {
            this.cmodalShow = true
        },
        onClick4 () {
            new CModal2()
        },
        onModalClick (msg) {
            this.cmodalShow = false
        }
    }
})

</script>
<style lang="less" scoped>
@import '../../components/less/layout.less';
.page {
    .flexbox();
    .flexbox.vertical();
    .flexbox.center();
    .content {
        .flexitem(1);
        .flexbox();
        .flexbox.vertical();
        .flexbox.center();
    }
}
</style>


